/**=====================
  38. Landing CSS Start
==========================**/
$landing-color: $primary-color;
.landing-page {
  .section-py-space {
    padding-top: 70px;
    padding-bottom: 70px;
  }
  .section-pt-space {
    padding-top: 70px;
  }
  .section-pb-space {
    padding-bottom: 70px;
  }
  .light-bg {
    background-color: rgb(62 75 173 / 4%) !important;
  }
  section {
    .title {
      margin-bottom: 35px;
      position: relative;
      text-align: center;
      width: fit-content;
      margin-left: auto;
      margin-right: auto;
      transition: all 0.3s ease;
      h2 {
        font-size: 60px;
        background: linear-gradient(82.15deg, #FF6150 0.56%, #6271EB 48.44%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
      }
    }
    &:hover {
      transition: all 0.3s ease;
      .title {
        h2 {
          &:after {
            width: 110%;
          }
        }
      }
    }
  }
  // landing header
  .custom-container {
    max-width: 1660px;
    padding-right: var(--bs-gutter-x, 0.75rem);
    padding-left: var(--bs-gutter-x, 0.75rem);
    margin-right: auto;
    margin-left: auto;
  }
  .navbar-nav {
    align-items: center;
    .nav-item {
      position: relative;
      .nav-link {
        color: $black;
        font-size: 15px;
        font-weight: 500;
        letter-spacing: 1px;
        padding: 12px;
        transition: all 0.3s ease;
        opacity: 0.7;
        &:hover,
        &.active {
          opacity: 1;
          text-shadow: 0 0 0 $primary-color;
          transition: all 0.3s ease;
          color: $primary-color;
        }
      }
    }
  }
  .btn-landing {
    color: $white;
    background-color: var(--theme-deafult);
    padding: 12px 30px;
    line-height: 1;
    font-weight: 600;
    display: flex;
    align-items: center;
    width: fit-content;
    &:hover {
      color: $white;
    }
    &.btn-white {
      background-color: $white !important;
      color: $primary-color;
    }
  }
  .landing-header {
    padding: 12px 40px;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 2;
    box-shadow: 0 0 12px rgba($white, 0.1);
    transition: all 0.5s ease-in-out;
    background-color: #dce2f9;
    &.sticky {
      background-color: #dce2f9;
      transition: all 0.5s ease-in-out;
      padding: 6px 10px;
    }
    ul.landing-menu {
      li.nav-item {
        &.menu-back {
          border-bottom: 1px solid $light-gray;
          padding: 22px 20px;
          text-align: right;
          text-transform: uppercase;
          font-weight: 700;
          font-size: 16px;
          display: none;
          cursor: pointer;
          margin-bottom: 15px;
          i {
            font-size: 16px;
            margin-left: 10px;
          }
        }
        a.nav-link {
          font-size: 16px;
          font-weight: 600;
          color: $theme-font-color;
          padding: 10px 20px;
        }
        &:hover {
          a.nav-link {
            color: $secondary-color;
          }
        }
      }
    }
    .buy-block {
      display: flex;
      align-items: center;
      .toggle-menu {
        margin-left: 20px;
        display: none;
        cursor: pointer;
        i {
          font-size: 24px;
          color: $white;
        }
      }
    }
  }
  .btn-section {
    margin-top: 25px;
    color:  $white !important;
    font-weight: 600;
    background-color: #f8f5fd;
    border-radius: 8px;
    font-size: calc(13px + (20 - 15) * ((100vw - 300px) / (1920 - 300)));
    background-color: var(--theme-deafult) !important;
    cursor: pointer;
    display: inline-block;
    &:hover {
      background-color: $black !important;
      color: $white !important;
    }
  }
  // landing home
  .landing-home {
    background-color: rgba($primary-color, 0.9);
    position: relative;
    overflow: hidden;
    .landing-home-contain {
      margin-left: 159px;
      text-align: left;
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;
      .transfer {
        display: inline-flex;
        align-items: center;
        img {
          width: 66px;
          height: 66px;
        }
        p {
          font-size: 18px;
          font-weight: 500;
          color: var(--theme-deafult);
          margin: 0;
        }
      }
      h2 {
        color: $theme-font-color;
        font-weight: 700;
        font-size: 60px;
        margin-bottom: 6px;
        line-height: 79px;
        span {
          background: linear-gradient(82.15deg, #FF6150 0.56%, #6271EB 48.44%);
          -webkit-background-clip: text;
          -webkit-text-fill-color: transparent;     
        }
        img {
          position: absolute;
          left: 0;
          top: 43%;
        }
      }
      h3 {
        font-size: 24px;
        color: $secondary-color;
        font-weight: 600;
        margin-bottom: 15px;
      }
      p {
        color:#535353;
        font-size: 18px;
        font-weight: 600;
        width: 100%;
        margin-bottom: 30px;
      }
      .btn-home-list {
        margin-bottom: -10px;
        li {
          display: inline-block;
          .btn {
            text-transform: capitalize;
            margin-bottom: 10px;
            display: flex;
            align-items: center;
            padding: 13px 15px;
            svg {
              width: 20px;
              height: 20px;
            }
            span {
              font-size: 16px;
              font-weight: 600;
            }
          }
          &:nth-child(n + 2) {
            margin-left: 10px;
          }
        }
      }
    }
    .animat-block {
      position: relative;
      padding: 195px 0;
      ul {
        // position: relative;
        li {
          img {
            border-radius: 15px;
            // transform: rotate(-12deg);
          }
          // &:first-child {
          //   animation: move2 1.2s infinite alternate;
          //   z-index: 1;
          //   margin-left: 40px;
          //   position: relative;
          // }
          &:first-child {
            animation: move2 1.3s infinite alternate;
            position: absolute;
            top: 9%;
            left: 39%;
          }
          &:nth-child(2) {
            position: absolute;
            bottom: 7%;
            left: 0%;
            z-index: 1;
            animation: move2 1.4s infinite alternate;
          }
          &:nth-child(3) {
            position: absolute;
            bottom: 0;
            right: 27px;
            z-index: 1;
            animation: move2 1.5s infinite alternate;
          }
        }
      }
    }
  }
  @keyframes move1 {
    0% {
      transform: rotate(0deg) translateX(15px) rotate(0deg);
    }
    100% {
      transform: rotate(360deg) translateX(15px) rotate(-360deg);
    }
  }
  @keyframes move2 {
    0% {
      transform: translateY(0);
    }
    100% {
      transform: translateY(-5px);
    }
  }
  // layout section
  .layout-section {
    background-color: $white;
    .layout-list {
      display: flex;
      justify-content: center;
      margin-bottom: 35px;
      border: none;
      .nav-item {
        .nav-link {
          padding: 14px 30px;
          background-color: rgba($primary-color, 0.1);
          border-radius: 30px;
          font-weight: 500;
          &.active {
            background-color: $primary-color;
            color: $white;
          }
        }
        + .nav-item {
          margin-left: 18px;
        }
      }
    }
    .layout-links {
      .row {
        margin-bottom: -30px;
        > div {
          margin-bottom: 30px;
        }
      }
      .layout-box {
        background-color: $white;
        padding: 15px;
        border-radius: 20px;
        box-shadow: 0 0 20px rgba(8, 21, 66, 0.2);
        ul{
          margin-top: 18px;
          align-items: center;
          justify-content: center;
          flex-wrap: wrap;
          img{
            border-radius: 0;
          }
          li{
            border: 1px solid rgba($primary-color, 0.5);
            border-radius: 14px;
            box-shadow: 0 0 20px rgba(8, 21, 66, 0.1);
            a{
              padding: 9px;
              display: flex;
              align-items: center;
            }
            + li{
              margin-left: 12px;
            }
            &:nth-last-child(-n+2){
              margin-top: 10px;
            }
          }
        }
        img {
          transition: all 0.5s ease;
          border-radius: 10px;
        }
        &:hover {
          img {
            transform: scale(1.02);
          }
        }
      }
      .nav-link {
        border-radius: 10px;
        padding: 15px 30px;
        align-items: center;
        background-color: rgba(92, 97, 242, 0.8);
        position: relative;
      }
    }
  }
  // demo section
  .demo-section {
    .demo-box {
      background-color: $white;
      padding: 20px;
      border-radius: 20px;
      .demo-title {
        padding-top: 20px;
        text-align: center;
        .btn {
          text-transform: capitalize;
          border: 1px solid $primary-color;
          padding: 8px 15px;
          transition: all 0.5s ease;
          color: $primary-color;
          &:hover {
            background-color: $primary-color;
            color: $white;
          }
        }
      }
      .img-wrraper {
        border-radius: 10px;
        overflow: hidden;
        position: relative;
        img {
          transition: all 0.5s ease;
          width: 100%;
        }
      }
      &:hover {
        .img-wrraper {
          img {
            transform: scale(1.02);
          }
        }
      }
    }
    .demo-block {
      margin-bottom: -30px;
      justify-content: center;
      > div {
        margin-bottom: 30px;
      }
    }
  }
  // Frameworks
  .framework {
    .nav {
      margin-bottom: 35px;
      .nav-item {
        &:nth-child(n + 2) {
          margin-left: 20px;
        }
      }
      .nav-link {
        border-radius: 14px;
        padding: 14px;
        align-items: center;
        background-color: $white;
        border: 1px solid $primary-color;
        position: relative;
        h5 {
          font-weight: 700;
          color: $primary-color;
        }
        p {
          color: $white;
        }
        &.active,
        &.show {
          background-color: rgba($primary-color, 0.2);
          border: 1px solid $primary-color;
          box-shadow: 0 0 20px rgba(8, 21, 66, 0.10);
        }
        img {
          height: 28px;
          margin-right: 8px;
        }
      }
    }
    ul.framworks-list {
      text-align: center;
      li {
        display: inline-block;
        width: 202px;
        margin-bottom: 45px;
        &:hover{
          .framworks-img {
            img {
              &::after{
                opacity: 1;
                -webkit-animation: 1.5s ease-out 75ms normal none infinite running shine;
                animation: 1.5s ease-out 75ms normal none infinite running shine;
              }
            }
          }
        }
        .framworks-img {
          padding: 40px 10px;
          margin-bottom: 30px;
          border-radius: 22%;
          border: 2px solid rgba(0, 26, 255, 0.1);
          margin: 10px;
          height: 130px;
          width: 130px;
          transition: all 0.3s ease;
          text-align: center;
          margin: 0 auto;
          box-shadow: 0px 5px 20px rgba(46, 35, 94, 0.12);
          img {
            transition: all 0.5s ease;
            height: 58px;
            &::after {
              border-radius: 50%;
              box-shadow: 0 0 0 2px rgba(255,255,255,.2);
              content: "";
              height: 100%;
              width: 100%;
              left: 0;
              top: 0;
              position: absolute;
              opacity: 0;
              -webkit-transform: scale(.9);
              transform: scale(.9);
              z-index: -1;
            }
          }
        }
        h6 {
          margin-top: 10px;
          margin-bottom: unset;
          transition: all 0.5s ease;
          font-weight: 600;
          font-size: 16px;
        }
        &:hover {
          img {
            transform: scale(0.9);
          }
          h6 {
            color: $primary-color;
          }
        }
      }
    }
  }
  // responisve card
  .responsive-card {
    .img-wrraper {
      text-align: center;
    }
  }
